<template>
  <div class="list"
       ref="wrapper">
    <div>
      <div class="area">
        <div class="title border-topbottom">当前城市</div>
        <div class="button-list">
          <div class="button-wrapper">
            <div class="button">{{this.currentCity}}</div>
          </div>
        </div>
      </div>
      <div class="area">
        <div class="title border-topbottom">热门城市</div>
        <div class="button-list">
          <div class="button-wrapper"
               v-for="item in hotCities"
               :key="item.id">
            <div class="button"
                 @click="cityClick(item.name)">{{item.name}}</div>
          </div>
        </div>
      </div>
      <div class="area"
           v-for="(items,key) in cities"
           :key="key"
           :ref="key">
        <div class="
           title
           border-topbottom">{{key}}</div>
        <div class="item-list"
             v-for="item in items"
             :key="item.id">
          <div class="item border-bottom"
               @click="cityClick(item.name)">
            {{item.name}}
          </div>
        </div>
      </div>

    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import BScroll from 'better-scroll'
import { mapState, mapMutations } from 'vuex'
export default {
  name: 'citylist',
  computed: {
    ...mapState({
      currentCity: 'city'
    })
  },
  props: {
    hotCities: Array,
    cities: Object,
    letter: String
  },
  methods: {
    cityClick (e) {
      // this.$store.commit('changeCity', e)
      this.changeCity(e)
      this.$router.push('/')
    },
    ...mapMutations(['changeCity'])
  },
  mounted () {
    this.scroll = new BScroll(this.$refs.wrapper)
  },
  components: {

  },
  watch: {
    letter () {
      const emlemt = this.$refs[this.letter][0]
      this.scroll.scrollToElement(emlemt)
    }
  }
}
</script>

<style scoped lang="stylus">
@import '~styles/varibles.styl';

.border-bottom:before {
  border-color: #cccccc;
}

.border-topbottom:before {
  border-color: #cccccc;
}

.border-topbottom:after {
  border-color: #cccccc;
}

.list {
  position: absolute;
  overflow: hidden;
  width: 100%;
  top: 1.78rem;
  left: 0;
  bottom: 0;
}

.button-list {
  padding: 0.1rem;
  overflow: hidden;
  padding: 0.1rem 0.6rem 0.1rem 0.1rem;
}

.button-wrapper {
  float: left;
  width: 33.33%;
}

.button {
  margin: 0.1rem;
  text-align: center;
  border: 0.02rem solid #cccccc;
  padding: 0.1rem 0;
}

.title {
  line-height: 0.54rem;
  background: #eeeeee;
  padding-left: 0.2rem;
}

.item-list .item {
  line-height: 0.76rem;
  color: #666;
  padding-left: 0.2rem;
}
</style>



