<template>
  <div>
    <div class='list'
         v-for='(item,index) in list'
         :key='index'>
      <div class="item-title border-bottom">
        <span class="item-title-icon"></span>
        {{item.title}}
      </div>
      <div v-if="item.children"
           class="item-children">
        <detailList :list="item.children"></detailList>
      </div>
    </div>
  </div>

</template>

<script type="text/ecmascript-6">
export default {
  name: 'detailList',
  computed: {

  },
  props: {
    list: Array
  }
}
</script>

<style scoped lang="stylus">
@import '~styles/varibles.styl';

.item-title {
  line-height: 0.8rem;
  font-size: 0.32rem;
  padding: 0 0.2rem;
}

.item-title-icon {
  display: inline-block;
  position: relative;
  width: 0.36rem;
  height: 0.36rem;
  top: 0.1rem;
  left: 0.1em;
  background: url('http://s.qunarzz.com/piao/image/touch/sight/detail.png') 0 -0.45rem no-repeat; // s.qunarzz.com/piao/image/touch/sight/detail.png) 0 -0.45rem no-repeat;
  margin-right: 0.1rem;
  background-size: 0.4rem 3rem;
}

.item-children {
  padding: 0 0.2rem;
}
</style>



