<template>
  <div class="page">
    <detailheade></detailheade>
    <detailbanner :name='sightName'
                  :bannerImg='bannerImg'
                  :gallaryImgs="gallaryImgs"></detailbanner>
    <div class="content">
      <detaillist :list='list'></detaillist>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import detailbanner from './commponents/banner'
import detailheade from './commponents/header'
import detaillist from './commponents/list'
import axios from 'axios'

export default {
  name: 'detail',
  data () {
    return {
      sightName: '',
      bannerImg: '',
      gallaryImgs: [],
      list: []
    }
  },
  components: {
    detailbanner,
    detailheade,
    detaillist
  },
  methods: {
    getDateInfo () {
      axios.get('./api/detail.json', {
        params: {
          id: this.$route.params.id
        }
      }).then(this.handleGetDataSucc)
    },
    handleGetDataSucc (res) {
      res = res.data
      if (res.ret && res.data) {
        const data = res.data
        console.log(data)
        this.sightName = data.sightName
        this.list = data.categoryList
        this.bannerImg = data.bannerImg
        this.gallaryImgs = data.gallaryImgs
      }
    }

  },
  mounted () {
    this.getDateInfo()
  }
}
</script>

<style scoped lang="stylus">
#test {
  height: 100rem;
  width: 100%;
  background: #FFF;
}
</style>



