<template>
  <div class="container"
       @click="close">
    <div class="wrapper">
      <swiper :options="swiperOption">
        <swiper-slide v-for="(item,index) in img"
                      class="swiper"
                      :key="index"><img class="swiper-img"
               :src="item" /></swiper-slide>

      </swiper>
      <div class="swiper-pagination"
           slot="pagination"></div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
export default {
  name: 'commonGaller',
  props: {
    img: {
      type: Array
    }
  },
  data () {
    return {
      swiperOption: {
        pagination: '.swiper-pagination',
        paginationType: 'fraction',
        observer: true,
        observeParents: true
      }
    }
  },
  components: {

  },
  methods: {
    close () {
      this.$emit('closed')
      console.log('1')
    }
  }
}
</script>

<style scoped lang="stylus">
.container >>> .wrapper {
  overflow: invert;
}

.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  z-index: 99;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: #000;
}

.swiper-img {
  width: 100%;
  vertical-align: middle;
}

.swiper-pagination {
  color: #fff;
  bottom: -1rem;
  position: absolute;
  bottom: 24px;
}
</style>



