<template>
  <div class="page">
    <cityheader></cityheader>
    <citysearch :cities="cities"></citysearch>
    <citylist :letter="letter"
              :cities="cities"
              :hotCities="hotCities"></citylist>
    <atoz :cities="cities"
          @change="citychange"></atoz>
  </div>
</template>

<script type="text/ecmascript-6">
import cityheader from './components/cityHeader'
import citysearch from './components/search'
import citylist from './components/list'
import atoz from './components/Atoz'
import axios from 'axios'
export default {
  name: 'city',
  components: {
    cityheader,
    citysearch,
    citylist,
    atoz
  },
  data () {
    return {
      cities: {},
      hotCities: [],
      letter: ''
    }
  },
  methods: {
    getCityInfo () {
      axios.get('./api/city.json').then(this.getCityInfoSucc)
    },
    getCityInfoSucc (res) {
      console.log(res)
      res = res.data
      if (res.ret && res.data) {
        const data = res.data
        this.cities = data.cities
        this.hotCities = data.hotCities
      }
    },
    citychange (e) {
      console.log(e)
      this.letter = e
    }
  },
  mounted () {
    this.getCityInfo()
  }
}
</script>

<style scoped lang="stylus"></style>



