<template>
  <div class="header">
    <div class="header-left">
      <div class="iconfont back-icon">&#xe624;</div>
    </div>
    <div class="header-input">
      <span class="iconfont search-icon">&#xe632;</span>
      输入城市/景点/游玩主题</div>
    <router-link to="/city">
      <div class="head-right">{{this.city}}<span class="iconfont city-icon">&#xe64a;</span>
      </div>
    </router-link>
  </div>
  <!-- 1rem=html font-size =50px  -->
</template>

<script>
import { mapState } from 'vuex'
export default {
  name: 'homeHead',
  computed: {
    ...mapState(['city'])
  }
}
</script>

<style lang="stylus" scoped>
@import '~styles/varibles.styl';

.back-icon {
  text-align: center;
  font-size: 0.4rem;
}

.city-icon {
  text-align: center;
  font-size: 0.24rem;
  margin-right: 0.1rem;
  color: #fff;
  margin-left: 0.1rem;
}

.search-icon {
  margin-left: 0.1rem;
}

.header {
  display: flex;
  line-height: 0.86rem;
  background-color: $bgColor;
  color: #fff;
}

.header-left {
  width: 0.64rem;
  float: left;
}

.header-input {
  height: 0.64rem;
  flex: 1;
  line-height: 0.64rem;
  background: #FFF;
  color: #ccc;
  border-radius: 0.1rem;
  margin-top: 0.12rem;
  margin-left: 0.18rem;
  margin-right: 0.18rem;
}

.head-right {
  min-width: 1.04rem;
  padding: 0 0.1rem;
  float: right;
  text-align: center;
  color: #fff;
  height: 0.64rem;
}
</style>



