<template>
  <div class="swiper">
    <swiper :options="swiperOption"
            v-if="showSwiper">
      <!-- slides -->
      <swiper-slide v-for="item in swiperList"
                    :key="item.id"><img class="swiper-img"
             :src="item.imgUrl" /></swiper-slide>
      <!-- Optional controls -->
      <div class="swiper-pagination"
           slot="pagination"> </div>

    </swiper>
  </div>
</template>
<script>
export default {
  name: 'homeSwiper',
  props: {
    swiperList: Array
  },
  data () {
    return {
      swiperOption: {
        pagination: '.swiper-pagination',
        loop: true,
        autopaly: true
      }
    }
  },
  computed: {
    showSwiper () {
      return this.swiperList.length
    }
  }
}
</script>
<style lang="stylus" scoped>
.swiper >>>.swiper-pagination-bullet-active {
  background: #fff !important;
}

.swiper-img {
  width: 100%;
}

.swiper {
  width: 100%;
  height: 0;
  overflow: hidden;
  padding-bottom: 31.5%;
  background-color: #eee;
}
</style>



