<template>
  <div class="page">
    <div class="title">周末去哪儿</div>
    <ul>
      <li class="item border-bottom"
          v-for="item in weekend"
          :key="item.id">

        <div class="item-img-wrap">
          <img class="item-img"
               :src="item.imgUrl">
        </div>

        <div class="item-info">
          <p class="item-title">{{item.title}}</p>
          <p class="item-desc">{{item.desc}}</p>
        </div>
      </li>
    </ul>
  </div>
</template>

<script type="text/ecmascript-6">
export default {
  name: 'recommend',
  props: {
    weekend: Array
  },
  data () {
    return {

    }
  },
  components: {

  }
}
</script>

<style scoped lang="stylus">
@import '~styles/mixins.styl';

.title {
  line-height: 0.8rem;
  background-color: #eee;
  text-indent: 0.2rem;
}

.item-img-wrap {
  overflow: hidden;
  height: 0;
  padding-bottom: 37.09%;
}

.item-img {
  width: 100%;
}

.item-info {
  padding: 0.1rem;
}

.item-title {
  line-height: 0.54rem;
  font-size: 0.32rem;
  ellipsis();
}

.item-desc {
  line-height: 0.4rem;
  color: #cccccc;
  ellipsis();
}
</style>



