<template>
  <ul class="list">
    <li class="item"
        v-for="item in letter"
        :key="item"
        :ref="item"
        @click="choose"
        @touchstart.prevent="start"
        @touchmove="move"
        @touchend="end">{{item}}</li>
  </ul>

</template>

<script type="text/ecmascript-6">
export default {
  name: 'AtoZ',
  props: {
    cities: Object
  },
  data () {
    return {
      status: false,
      startY: 0,
      timer: null
    }
  },
  updated () {
    this.startY = this.$refs['A'][0].offsetTop
  },
  methods: {
    choose (e) {
      this.$emit('change', e.target.innerText)
    },
    start () {
      this.status = true
    },
    move (e) {
      if (this.status) {
        if (this.timer) {
          clearTimeout(this.timer)
        }
        this.timer = setTimeout(() => {
          const touchY = e.touches[0].clientY - 79
          const index = Math.floor((touchY - this.startY) / 20)
          if (index >= 0 && index < this.letter.length) {
            this.$emit('change', this.letter[index])
          }
        }, 50)
      }
    },
    end () {
      this.status = false
    }
  },
  computed: {
    letter () {
      const letter = []
      for (let i in this.cities) {
        letter.push(i)
      }
      return letter
    }
  }
}
</script>

<style scoped lang="stylus">
@import '~styles/varibles.styl';

.list {
  position: absolute;
  top: 1.8rem;
  right: 0;
  bottom: 0;
  width: 0.4rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.item {
  line-height: 0.4rem;
  text-align: center;
  color: $bgColor;
}
</style>



