<template>
  <div>
    <div class="search">
      <input class="search-input"
             type="text"
             v-model="keyword"
             placeholder="请输入城市或者拼音">
    </div>

    <div class="search-content"
         v-show="keyword"
         ref="search">
      <ul>
        <li class="search-item border-bottom"
            v-for="item of list"
            :key="item.id"
            @click="cityClick(item.name)">
          {{item.name}}
        </li>
        <li class="search-item border-bottom"
            v-show="!list.length">
          没有找到匹配数据
        </li>
      </ul>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import BScroll from 'better-scroll'
export default {
  name: 'citySearch',
  props: {
    cities: Object
  },
  data () {
    return {
      keyword: '',
      list: [],
      timer: null
    }
  },
  components: {

  },
  methods: {
    cityClick (e) {
      this.$store.commit('changeCity', e)
      console.log(e)
      this.$router.push('/')
    }
  },
  mounted () {
    this.scroll = new BScroll(this.$refs.search)
  },
  watch: {
    keyword () {
      if (this.timer) {
        clearTimeout(this.timer)
      }
      if (!this.keyword) {
        this.list = []
        return
      }
      this.timer = setTimeout(() => {
        const result = []
        for (let i in this.cities) {
          this.cities[i].forEach(element => {
            if (element.spell.indexOf(this.keyword) > -1 || element.name.indexOf(this.keyword) > -1) {
              result.push(element)
            }
          })
        }
        this.list = result
      }, 100)
    }
  }
}
</script>

<style scoped lang="stylus">
@import '~styles/varibles.styl';

.search {
  height: 0.72rem;
  background: $bgColor;
  padding: 0.1rem;
}

.border-bottom:before {
  border-color: #cccccc;
}

.search-input {
  box-sizing: border-box;
  width: 100%;
  height: 0.62rem;
  line-height: 0.62rem;
  text-align: center;
  borde-radius: 0.06rem;
  padding: 0 0.1rem;
}

.search-content {
  overflow: hidden;
  position: absolute;
  top: 1.62rem;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #eee;
  z-index: 1;
}

.search-item {
  line-height: 0.62rem;
  padding: 0.2rem;
  color: #666;
  background: #fff;
}
</style>



