<template>
  <div class="icons">
    <swiper class="swiper"
            :options="swiperOption">
      <swiper-slide v-for="(page,index) in pages"
                    :key="index">
        <div class="icon"
             v-for="item in page"
             :key="item.id">
          <div class="icon-img">
            <img class="icon-img-content"
                 :src="item.imgUrl">
          </div>
          <p class="icon-desc">{{item.desc}}</p>
        </div>
      </swiper-slide>

    </swiper>

  </div>

</template>
<script>
export default {
  name: 'icons',
  props: {
    iconList: Array
  },
  data () {
    return {
      swiperOption: {
        autopaly: false
      }
    }
  },
  computed: {
    pages () {
      const pages = []
      this.iconList.forEach((item, index) => {
        const page = Math.floor(index / 8)
        console.log(page)
        if (!pages[page]) {
          pages[page] = []
        }
        pages[page].push(item)
      })
      return pages
    }
  }

}
</script>
<style lang="stylus" scoped>
@import '~styles/varibles.styl';
@import '~styles/mixins.styl';

.icons >>> .swiper {
  height: 0;
  padding-bottom: 50%;
}

.icons {
  height: 0;
  overflow: hidden;
  padding-bottom: 50%;
  margin-top: 0.1rem;
  // background: green;
}

.icon {
  float: left;
  overflow: hidden;
  width: 25%;
  height: 0;
  padding-bottom: 25%;
  // background-color: red;
  position: relative;
}

.icon-img {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0.44rem;
  // background: blue;
  box-sizing: border-box;
  padding: 0.1rem;
}

.icon-img-content {
  display: block;
  margin: 0 auto;
  height: 100%;
}

.icon-desc {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  height: 0.44rem;
  line-height: 0.44rem;
  color: $darkTextColor;
  text-align: center;
  ellipsis();
}
</style>



