<template>
  <div class="page">
    <div class="title">热销推荐</div>
    <ul>
      <router-link :to="'/detail/'+item.id"
                   tag="li"
                   class="item border-bottom"
                   v-for="item in recommend"
                   :key="item.id">

        <img class="item-img"
             :src="item.imgUrl">

        <div class="item-info">
          <p class="item-title">{{item.title}}</p>
          <p class="item-desc">{{item.desc}}</p>
          <button class="item-button">查看详情</button>
        </div>
      </router-link>
    </ul>
  </div>
</template>

<script type="text/ecmascript-6">
export default {
  name: 'recommend',
  props: {
    recommend: Array
  },
  data () {
    return {

    }
  },
  components: {

  }
}
</script>

<style scoped lang="stylus">
@import '~styles/mixins.styl';

.title {
  margin-top: 0.2rem;
  line-height: 0.8rem;
  background-color: #eee;
  text-indent: 0.2rem;
}

.item {
  overflow: hidden;
  display: flex;
  height: 1.9rem;
  // background-color: red;
}

.item-img {
  width: 1.7rem;
  height: 1.7rem;
  padding: 0.1rem;
}

.item-info {
  flex: 1;
  min-width: 0;
  padding: 0.1rem;
}

.item-title {
  line-height: 0.54rem;
  font-size: 0.32rem;
  ellipsis();
}

.item-desc {
  line-height: 0.4rem;
  color: #cccccc;
  ellipsis();
}

.item-button {
  background: #ff9300;
  line-height: 0.44rem;
  margin-top: 0.16rem;
  padding: 0 0.2rem;
  border-radius: 0.06rem;
  color: #fff;
}
</style>



