<template>
  <div>
    <router-link to="/"
                 tag="div"
                 class="header-abs"
                 v-show='showAbs'>
      <div class="iconfont back-icon">&#xe624;</div>
    </router-link>
    <div class="header-fixed"
         :style="opacityStyle"
         v-show='!showAbs'>
      <router-link to="/"
                   tag="div">
        <div class="iconfont header-fixed-icon">&#xe624;</div>
      </router-link>
      景点详情
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
export default {
  name: 'detailheader',
  data () {
    return {
      showAbs: true,
      opacityStyle: {
        opacity: 0
      }
    }
  },
  components: {

  },
  methods: {
    handlescroll () {
      const top = document.documentElement.scrollTop

      if (top > 60) {
        this.showAbs = false
        let opacity = top / 140
        opacity = opacity > 1 ? opacity : 1
        this.opacityStyle = {
          opacity
        }
      } else {
        this.showAbs = true
      }
      console.log(top)
    }
  },
  activated () {
    window.addEventListener('scroll', this.handlescroll)
  },
  deactivated () {
    window.removeEventListener('scroll', this.handlescroll)
  }
}
</script>

<style scoped lang="stylus">
@import '~styles/varibles.styl';

.header-fixed {
  height: $headerHeight;
  line-height: $headerHeight;
  background-color: $bgColor;
  color: #fff;
  text-align: center;
  font-size: 0.32rem;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1;
}

.header-abs {
  position: absolute;
  left: 0.2rem;
  top: 0.2rem;
  width: 0.8rem;
  height: 0.8rem;
  border-radius: 0.4rem;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 1;
}

.back-icon {
  text-align: center;
  font-size: 0.4rem;
  color: #fff;
  line-height: 0.8rem;
}

.header-fixed-icon {
  position: absolute;
  top: 0;
  left: 0;
  width: 0.64rem;
  text-align: center;
  font-size: 0.4rem;
  color: #fff;
  line-height: 0.8rem;
  z-index: 1;
}
</style>



