<template>
  <div>
    <div class="banner"
         @click="bannerclick">
      <img class="banner-img"
           :src="bannerImg">
      <div class="banner-info">
        <div class="banner-title">{{this.name}}</div>
        <div class="banner-number"><span class="iconfont search-icon">&#xe674;</span>{{this.gallaryImgs.length}}</div>
      </div>
    </div>
    <fade>
      <commonGallary :img="gallaryImgs"
                     @closed="closed"
                     v-show="bannerclicks"></commonGallary>
    </fade>
  </div>

</template>

<script type="text/ecmascript-6">
import commonGallary from 'common/galler/galler'
import fade from 'common/fade/fade'
export default {
  name: 'detailbanner',
  data () {
    return {
      bannerclicks: false
    }
  },
  components: {
    commonGallary,
    fade
  },
  props: {
    name: String,
    bannerImg: String,
    gallaryImgs: Array
  },
  mounted () {
    console.log(this.sightName)
  },
  methods: {
    bannerclick () {
      this.bannerclicks = true
    },
    closed () {
      this.bannerclicks = false
      console.log(22)
    }
  }
}
</script>

<style scoped lang="stylus">
.banner {
  height: 0;
  padding-bottom: 55%;
  position: relative;
  color: #fff;
}

.banner-img {
  width: 100%;
}

.banner-info {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  line-height: 0.6rem;
}

.banner-title {
  flex: 1;
  font-size: 0.32rem;
  padding: 0 0.2rem;
  float: left;
}

.banner-number {
  padding: 0 0.15rem;
  line-height: 0.32rem;
  height: 0.32rem;
  font-size: 0.24rem;
  margin-top: 0.15rem;
  border-radius: 0.2rem;
  background: rgba(0, 0, 0, 0.8);
  width: auto;
  display: inline-block;
  float: right;
}

.search-icon {
  font-size: 0.24rem;
}
</style>



